
import { computed, defineComponent } from "vue"
import { Button, Dialog, Divider, Toast } from "vant"
import OrderService from "@/services/order"
import { useRouter } from "vue-router"

export default defineComponent({
  name: "OrderListCard",
  components: {
    [Divider.name]: Divider,
    [Button.name]: Button,
  },
  props: {
    orderNo: String,
    number: [String, Number],
    status: String,
    productname: String,
    producttype: String,
    chuangjiantime: String,
    zhifushuliang: String,
  },
  setup(props) {
    const { go } = useRouter()
    const paid = computed(() => props.status !== "")

    function onCancel() {
      Dialog.confirm({
        title: "",
        message: "",
        cancelButtonText: "",
        confirmButtonText: "",
      }).then(() => {
        OrderService.cancelOrder(props.orderNo).then(({ status, message }) => {
          Toast(message)
          if (status === 200) {
            go(0)
          }
        })
      })
    }

    return {
      paid,
      onCancel,
    }
  },
})
